@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #fff;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

.community-padding{
padding-top: 100px;
}

.end-icons-container{
  padding-top: 100px;
}

.end-icons{
  padding: 0 20px 30px 20px;
}

.end-text{
 opacity: 0.3;
 font-family: 'Source Sans Pro';
 font-size: 12px;
 font-weight: 600;
 font-stretch: normal;
 font-style: normal;
 line-height: normal;
 letter-spacing: normal;
 text-align: center;
 color: #000000;
 padding-bottom: 40px;
}

@media screen and (min-width:360px) and (max-width: 500px) {
  a.nav_links{
    font-size: 12px;
    padding-right: 0.25rem;
  }
  div.logo{
    font-size: 13px;
    padding-top: 4px;
  }
  a.nav_login{
    font-size: 12px;
  }
  .login-padding{
    padding: 0;
  }
}

@media screen and (min-width:500px) and (max-width: 576px) {
  a.nav_links{
    font-size: 13px;
    padding-right: 0.33rem;
  }
  div.logo{
    font-size: 14px;
    padding-top: 4px;
  }
  a.nav_login{
    font-size: 13px;
  }
  .login-padding{
    padding: 0;
  }
}

a:hover{
  color:#540cfa;
}

a:link, a:visited, a:active{
  text-decoration: none;
}

a:active{
  color:black;
}

.nav {
  overflow: hidden;
  position: fixed;
  top:0;
  width: 100%;
  height: 40px;
  padding-top: 5px;
  background-color: white;
  z-index: 999999;
  border-bottom: 1px solid #DBDEDB;
}

.logo {
  height: 30px;
  font-family: 'Source Sans Pro';
  font-size: 18px;
  font-weight: bold;
}

#track{
  color: #540cfa;
}

.nav_links{
  height: 30px;
  font-family: 'Source Sans Pro';
  font-size: 14px;
  font-weight: bold;
  color: #000000;
  padding-right: 1rem;
}

.nav_login{
  height: 30px;
  font-family: 'Source Sans Pro';
  font-size: 14px;
  font-weight: bold;
  color: #000000;
}

@media screen and (max-width: 576px) {
  div.home-header {
    font-size: 15px;
  }
  div.home-paragraph{
    font-size: 15px
  }
  img.phone-image{
    width: 150px;
    height: auto;
  }
  h1.home-header{
    text-align: center;
  }
  p.home-paragraph{
    text-align: center;
    width: 100%;
  }
  div.email-form-component{
    padding-left: 25px;
  }
  div.companies-images-parent{
    text-align: center;
  }
  img.companies-image{
    height: 23px;
    width: auto;
  }
}

@media screen and (min-width:576px) and (max-width: 768px) {
  h1.home-header{
    text-align: center;
  }
  p.home-paragraph{
