.home-content{
  text-align: left;
  font-family: 'Source Sans Pro';
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
}

.home-header {
  font-weight: 700;
  line-height: 1.03;
}

.home-paragraph{
