.email-input {
  width: 100%;
  height: 40px;
  border-radius: 4px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05), 0 6px 12px 0 rgba(0, 0, 0, 0.05);
  border: solid 0.5px rgba(0, 0, 0, 0.05);
  background-color: #ffffff;
}

.email-submit {
  height: 40px;
  font-family: 'Source Sans Pro';
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  background-color: #540cfa;
  cursor: pointer;
}

.email-submit:hover{
  background-color: #8350fb;
}

#email_button {
  display: block;
  overflow: hidden;
}

.email-padding{
  padding: 0;
}

